/* print.css */
@media print {
    body {
      font-size: 12px;
      color: black;
    }
  
    .header, .tabs, .buttons {
      display: none; /* Hide header and buttons on print */
    }
  
    .section-card, .summary-section {
      border: 1px solid black;
      padding: 10px;
      margin: 10px 0;
    }
  
    .summary-container {
      border: 1px solid black;
      padding: 10px;
    }
  
    h1, h2 {
      font-size: 18px;
    }
  
    table {
      width: 100%;
      border-collapse: collapse;
    }
  
    table, th, td {
      border: 1px solid black;
    }
  
    th, td {
      padding: 8px;
      text-align: left;
    }
  }
  